@import 'reset';


* {
	box-sizing: border-box;
}

body {
	// background: #fff;
	
}

.hero-section{
	width: 100%;
	height: 100vh;
	overflow: hidden;


	.topLine{
	
		@media screen and (min-width: 768px){
			width: 198.194444%;
		}
		@media screen and (min-width: 1024px){
			width: 140.194444%;
		}
		@media screen and (min-width: 1224px){
			width: 112.194444%;
		}
		@media screen and (min-width: 1440px){
			width: 93.19444444%;
		}
		display: flex;
		flex-direction: row;

		div:first-child{
			background-image: url('../img/purpleTriLeft.png');
			background-repeat: no-repeat;
			background-size: contain;
			height: 0;
			left: 0;
			top: 0;
			@media screen and (max-width: 767px){
				
			}
			@media screen and (min-width: 768px){
				padding-bottom: 15%;
				width: 23.013889%;
			}
			@media screen and (min-width: 1024px){
				width: 22.013889%;
			}
			@media screen and (min-width: 1224px){
				width: 26.013889%;
			}
			@media screen and (min-width: 1440px){
				padding-bottom: 22%;
				width: 23.95833333%;
			}

		}

		div:nth-child(2){
			background-image: url('../img/topLogo.png');
			background-repeat: no-repeat;
			background-size: contain;
			height: 0;
			top: 0;
			@media screen and (min-width: 768px){
				padding-bottom: 21%;
				width: 52.013889%;
			}
			@media screen and (min-width: 1024px){
				padding-bottom: 23%;
    			width: 30.013889%;
			}
			@media screen and (min-width: 1224px){
				padding-bottom: 30%;
				width: 44.013889%;
			}
			@media screen and (min-width: 1440px){
				padding-bottom: 28%;
				width: 47.013889%;
			}
		}
	
		.burger{
			position: absolute;
			right: 176px;
			top: 68px;
	
			div:first-child{
				position: absolute;
				background-color: #825aa6;
				height: 7px;
				width: 67px;
				left: 24px;
				border-radius: 20px;
			}
	
			div:last-child{
				top: 22px;
				position: absolute;
				background-color: #93a1d2;
				height: 7px;
				width: 45px;
				left: 46px;
				border-radius: 20px;
			}
		}
	}
	
	.middleHero{
		display:flex;
		flex-direction: row;

		.heroText{
			color: #504099;
			text-align: center;
			@media screen and (min-width: 768px){
				width: 61.97916667%;
				margin: 0 auto;
			}
			@media screen and (min-width: 1440px){
				margin-left: 9.097222222%;
				margin-right: 57.98611111%;
				width: 33.05555556%;
			}
			
			h1{
				font-weight: 300;
				font-size: 4em;
				text-transform: uppercase;
				letter-spacing: 0.08em;
				font-family: 'Cabin Sketch', cursive;
				white-space: nowrap;
			}
	
			h2{
				font-family: 'Crimson Text', serif;
				font-size: 1.6em;
				margin-top: 21px;
				letter-spacing: 0.08em;
			}
	
			.line{
				margin: 36px auto;
				background-color: #504099;
				height: 2px;
				width: 40%;
				position: relative;
	
				div{ // dot
					background-color: #825aa6;
					height: O;
					width: 5%;
					padding-bottom: 5%;
					position: absolute;
					border-radius: 90%;
					margin: -4px 48% 0 48%;
				}
				
			}
	
			h3{
				text-transform: uppercase;
				letter-spacing: 0.09em;
				font-family: 'Cabin Sketch', cursive;
			}
	
			.down{
				width: 0; 
				height: 0; 
				border-left: 20px solid transparent;
				border-right: 20px solid transparent;
				border-top: 20px solid #825aa6;
				margin: 20px auto;
			}
		}


		.rtTri{
			@media screen and (max-width: 1223px){
				display: none;
			}
			@media screen and (min-width: 1224px){
				display: flex;
				background-image: url('../img/purpleTriRight.png');
				background-repeat: no-repeat;
				background-size: contain;
				height: 0;
				padding-bottom: 40%;
				width: 17.694444%;
				right: 0;
				position: absolute;
				margin-right: 0px;
				top: 173px;
			}

		}
	}
	
	

	.bottomLine{
		display: flex;
		flex-direction: row;
		@media screen and (min-width: 768px){
			width: 200.069444%;
		}
		@media screen and (min-width: 1024px){
			width: 170.069444%;
		}
		@media screen and (min-width: 1224px){
			// width: 170.069444%;
		}
		@media screen and (min-width: 1440px){
			width: 95.06944444%;
		}
		

		div:first-child{
			background-image: url('../img/leftLogo.png');
			background-repeat: no-repeat;
			background-size: contain;
			height: 0;
			left:0;
			bottom: 0;
			padding-right: 11.59722222%;
			@media screen and (min-width: 768px){
				margin-top: -46px;
				padding-bottom: 41%;
				width: 28.652778%;
			}
			@media screen and (min-width: 1024px){
				width: 17.652778%;
				margin-top: -108px;
				padding-bottom: 34%;
			}
			@media screen and (min-width: 1440px){
				padding-bottom: 36%;
				width: 19.65277778%;
			}
			
			
			
			
		}

		div:nth-child(2){
			background-color: #d2c3df;
			height: 4px;
			transform: rotate(-48deg);
			position: absolute;
			
			@media screen and (min-width: 768px){
				width: 72.375%;
				bottom: 173px;
				left: 34%;
			}
			@media screen and (min-width: 1024px){
				left: 31%;
			}
			@media screen and (min-width: 1224px){
				width: 41.375%;
				bottom: 150px;
				margin-left: 161px;
			}
			@media screen and (min-width: 1440px){
				width: 39.375%;
				bottom: 175px;
				left: 14%;
			}
		}

		div:nth-child(3){
			background-color: #d2c3df;
			height: 4px;
			transform: rotate(-48deg);
			position: absolute;

			@media screen and (min-width: 768px){
				width: 59.375%;
    			bottom: 135px;
				left: 42%;
			}
			@media screen and (min-width: 1024px){
				left: 42%;
			}
			@media screen and (min-width: 1224px){
				width: 40.375%;
				bottom: 55px;
				margin-left: 4%;
			}
			@media screen and (min-width: 1440px){
				width: 39.375%;
				bottom: 85px;
				left: 10%;
			}
		}

		div:last-child{
			background-image: url('../img/bottomLogo.png');
			background-repeat: no-repeat;
			background-size: contain;
			height: 0;
			padding-bottom: 45%;
			width: 154.013889%;
			@media screen and (min-width: 768px){
				margin-left: 24.333333%;
				margin-top: -119px;
			}
			@media screen and (min-width: 1024px){
				margin-left: 31.333333%;
    			margin-top: -186px;
			}
			@media screen and (min-width: 1440px){
				margin-left: 29.333333%;
				margin-top: -67px;
			}
			
			
		}
	}

}


.work{
	margin-top: 21%;
	background-image: url('../img/bkgText.png');
	background-repeat: no-repeat;
	background-size: contain;
	height: 0;
	padding-bottom: 100%;
}

